import React, { useMemo } from 'react';
import qs from 'querystring';
import grey from '@material-ui/core/colors/grey';
import { makeStyles } from '@material-ui/core';

export type CalendarMode = 'MONTH' | 'WEEK' | 'AGENDA';

const useStyles = makeStyles({
  iframe: {
    borderWidth: 0,
    width: '100%',
  },
});

interface CalendarProps {
  wkst?: boolean;
  bgColor?: string;
  color?: string;
  showTitle?: boolean;
  showDate?: boolean;
  showTabs?: boolean;
  showCalendars?: boolean;
  showNav?: boolean;
  height: number;
  mode?: CalendarMode;
}
const Calendar = ({
  wkst = true,
  bgColor,
  color,
  showTitle = false,
  showDate = false,
  showTabs = false,
  showCalendars = false,
  showNav = false,
  height = 600,
  mode = 'MONTH',
}: CalendarProps) => {
  const styles = useStyles();
  const host = 'https://calendar.google.com/calendar/embed';
  const src = 'Z3E1azY0OWpudXNtbW8zaTk3NjM5YjhkMThAZ3JvdXAuY2FsZW5kYXIuZ29vZ2xlLmNvbQ';
  const query = useMemo(() => {
    return qs.stringify({
      src,
      bgColor: bgColor ?? '#ffffff',
      color: color ?? grey[900],
      wkst: wkst ? '1' : '0',
      showTitle: showTitle ? '1' : '0',
      showDate: showDate ? '1' : '0',
      showTabs: showTabs ? '1' : '0',
      showCalendars: showCalendars ? '1' : '0',
      showNav: showNav ? '1' : '0',
      showTz: '1',
      showPrint: '0',
      height,
      ctz: 'Asia/Seoul',
      mode,
    });
  }, [bgColor, color, height, mode, showCalendars, showDate, showNav, showTabs, showTitle, wkst]);

  return (
    <iframe title="rpsCalendar" src={`${host}?${query}`} height={height} scrolling="no" className={styles.iframe} />
  );
};

export default Calendar;
