import { fetchDeployRuns, fetchCommits } from 'utils/github';
import { useState, useCallback, useEffect } from 'react';

export function useDeployments() {
  const [commits, setCommits] = useState([]);
  const [lastRunCommitSha, setLastRunCommitSha] = useState('');
  const [workflowId, setWorkflowId] = useState<number | undefined>();

  const fetchData = useCallback(async () => {
    const [
      {
        data: { runs, manualDeployWorkflowId },
      },
      { data: commits },
    ] = await Promise.all([fetchDeployRuns(), fetchCommits()]);
    setWorkflowId(manualDeployWorkflowId);
    setLastRunCommitSha(runs[0]?.head_sha ?? '');
    setCommits(commits);
  }, []);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  return {
    lastRunCommitSha,
    commits,
    fetchData,
    workflowId,
    setLastRunCommitSha,
  };
}
