import React, { useState, useCallback, ChangeEvent } from 'react';
import Page from 'components/Layout/Page';
import { Container, Grid, FormControl, RadioGroup, FormControlLabel, Radio } from '@material-ui/core';
import RpsIframeCalendar, { CalendarMode } from 'components/Utils/RpsIframeCalendar';

const RpsCalendar = () => {
  const [mode, setMode] = useState<CalendarMode>('MONTH');

  const handleModeChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value as CalendarMode;
    setMode(value);
  }, []);

  return (
    <Page
      pageName=" "
      showTitle={true}
      description={
        <>
                   ,     
          .
          <br />
                  .
        </>
      }
    >
      <Container>
        <Grid container spacing={3}>
          <Grid item xs={8}>
            <RpsIframeCalendar height={600} showDate={true} showNav={true} mode={mode} />
          </Grid>
          <Grid item xs={4}>
            <FormControl component="fieldset">
              <RadioGroup aria-label="calendar-mode" name="mode" value={mode} onChange={handleModeChange}>
                <FormControlLabel label="" value="MONTH" control={<Radio />} />
                <FormControlLabel label="" value="WEEK" control={<Radio />} />
                <FormControlLabel label="" value="AGENDA" control={<Radio />} />
              </RadioGroup>
            </FormControl>
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default RpsCalendar;
