import React, { useState, useCallback, ChangeEvent, Dispatch, SetStateAction, FormEvent } from 'react';
import { Button, TextField, makeStyles } from '@material-ui/core';
import { LoginScheme } from 'models/auth';
import Loading from 'components/Utils/Loading';

const useStyles = makeStyles({
  input: {
    marginBottom: '1rem',
  },
});

interface Props {
  onSubmit: (data: LoginScheme) => void;
  loading: boolean;
}
const EmailPasswordForm = ({ onSubmit, loading = false }: Props) => {
  const styles = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleInputChange = useCallback((handler: Dispatch<SetStateAction<string>>) => {
    return (e: ChangeEvent<HTMLInputElement>) => handler(e.target.value);
  }, []);

  const handleSubmit = useCallback(
    async (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      onSubmit({
        email,
        password,
      });
    },
    [email, onSubmit, password]
  );

  return (
    <form onSubmit={handleSubmit}>
      <TextField
        type="email"
        defaultValue={email}
        onChange={handleInputChange(setEmail)}
        placeholder=""
        fullWidth={true}
        disabled={loading}
        variant="outlined"
        className={styles.input}
      />
      <TextField
        type="password"
        defaultValue={password}
        onChange={handleInputChange(setPassword)}
        placeholder=""
        fullWidth={true}
        disabled={loading}
        variant="outlined"
        className={styles.input}
      />
      <Button fullWidth type="submit" variant="contained" disabled={loading} color="primary">
        {loading ? <Loading size={40} color="#ffffff" /> : ''}
      </Button>
    </form>
  );
};

export default EmailPasswordForm;
