import React, { useMemo } from 'react';
import { Card, Grid, CardContent, Typography, Avatar, makeStyles, Tooltip } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';
import { DRI } from 'src/models';
import { useAllMembers } from 'src/hooks/useAllMembers';
import { AvatarGroup } from '@material-ui/lab';

const useStyles = makeStyles({
  name: {
    margin: '1rem 0',
    fontWeight: 'bold',
  },
  avatar: {
    width: '3rem',
    height: '3rem',
  },
  avatarGroup: {
    marginTop: '1rem',
  },
});

interface Props {
  dri: DRI;
}
const DRICard = ({ dri: { name, description, members } }: Props) => {
  const lubyconMembers = useAllMembers();
  const driMembers = useMemo(() => {
    return lubyconMembers.filter(member => members.includes(member.id));
  }, [lubyconMembers, members]);

  const styles = useStyles();

  return (
    <AnimatedListItem>
      <Card>
        <CardContent>
          <Grid container direction="column" alignItems="center">
            <Grid item component={Typography} gutterBottom={true} className={styles.name}>
              {name}
            </Grid>
            <Grid item component={Typography} variant="caption">
              {description}
            </Grid>
            <Grid item justify="center">
              <AvatarGroup className={styles.avatarGroup}>
                {driMembers.map(member => (
                  <Tooltip title={member.name.en} aria-label={member.name.en}>
                    <Avatar
                      src={member.profileImg.childImageSharp.fixed.src}
                      alt={member.name.en}
                      className={styles.avatar}
                    />
                  </Tooltip>
                ))}
              </AvatarGroup>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </AnimatedListItem>
  );
};

export default DRICard;
