import React, { ReactNode, useCallback, useState } from 'react';
import { List, ListItem, Divider, Card, Typography, makeStyles, Theme, Button } from '@material-ui/core';
import format from 'date-fns/format';
import { deploy } from 'utils/github';
import Emoji from 'components/Utils/Emoji';
import purple from '@material-ui/core/colors/purple';

const useListStyles = makeStyles<Theme>(theme => ({
  root: {
    padding: theme.spacing(2),
  },
}));
const useItemStyles = makeStyles<Theme>(theme => ({
  root: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    paddingRight: 90,
    borderRadius: theme.shape.borderRadius,
    flexDirection: 'column',
    alignItems: 'flex-start',
  },
  head: {
    position: 'absolute',
    width: 3,
    height: '100%',
    top: 0,
    left: 0,
    backgroundColor: theme.palette.primary.main,
    borderRadius: theme.shape.borderRadius,
  },
  title: {
    fontWeight: theme.typography.fontWeightBold,
  },
  sha: {
    color: theme.palette.text.secondary,
  },
  date: {
    color: theme.palette.text.hint,
  },
  rightPosition: {
    position: 'absolute',
    right: 10,
    top: '50%',
    transform: 'translateY(-50%)',
    fontSize: theme.typography.caption.fontSize,
  },
  deployedBadge: {
    backgroundColor: purple['300'],
    borderRadius: theme.shape.borderRadius,
    paddingTop: theme.spacing(0.5),
    paddingBottom: theme.spacing(0.5),
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
    color: 'white',
  },
}));

interface ListProps {
  children: ReactNode;
}
const DeploymentList = ({ children }: ListProps) => {
  const styles = useListStyles();
  return (
    <Card className={styles.root}>
      <List>{children}</List>
    </Card>
  );
};

interface ItemProps {
  message: string;
  url: string;
  date: string;
  sha: string;
  workflowId: number;
  isDeployed?: boolean;
  underline?: boolean;
  onDeployed?: (sha: string) => void;
}
const DeploymentListItem = ({ message, date, url, sha, underline, workflowId, isDeployed, onDeployed }: ItemProps) => {
  const styles = useItemStyles();
  const [isLoading, setLoading] = useState(false);

  const handleDeployButtonClick = useCallback(
    async (e: any) => {
      e.preventDefault();
      setLoading(true);

      try {
        await deploy(sha, workflowId);
        await new Promise(resolve => setTimeout(resolve, 10000)); // 10 
        onDeployed?.(sha);
      } finally {
        setLoading(false);
      }
    },
    [onDeployed, sha, workflowId]
  );

  return (
    <>
      <ListItem button className={styles.root} href={url} target="blank" component="a">
        {isDeployed && <span className={styles.head} />}
        <Typography variant="subtitle1" className={styles.title}>
          {message}
        </Typography>
        <Typography variant="body2" gutterBottom className={styles.sha}>
          {sha}
        </Typography>
        <Typography variant="caption" className={styles.date}>
          {format(new Date(date), 'yyyy-MM-dd HH:mm')}
        </Typography>
        {isDeployed ? (
          <span className={`${styles.rightPosition} ${styles.deployedBadge}`}>
            <Emoji emoji="" name="checked" />  
          </span>
        ) : (
          <Button
            size="small"
            color="primary"
            variant="contained"
            disabled={isLoading}
            className={styles.rightPosition}
            onClick={handleDeployButtonClick}
          >
            {isLoading ? '...' : ''}
          </Button>
        )}
      </ListItem>
      {underline && <Divider variant="middle" />}
    </>
  );
};

DeploymentList.Item = DeploymentListItem;

export default DeploymentList;
