import React, { useMemo } from 'react';
import { Box, Card, Grid, CardContent, Typography, CardActionArea, makeStyles, Chip, Theme } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';
import { Tool } from 'src/models';
import { useAllMembers } from 'src/hooks/useAllMembers';

const useStyles = makeStyles<Theme>(theme => ({
  root: {
    textDecoration: 'none',
  },
  avatar: {
    width: '2rem',
    height: '2rem',
  },
  name: {
    margin: '1rem 0',
    fontWeight: 'bold',
  },
  image: {
    width: '3rem',
    height: '3rem',
    overflow: 'hidden',
  },
  chips: {
    padding: theme.spacing(2),
    '& .MuiChip-root': {
      marginRight: theme.spacing(1),
      '&:last-child': {
        marginRight: 0,
      },
    },
  },
}));

interface Props {
  tool: Tool;
}
const ToolCard = ({ tool: { name, url, icon, description, dri, admin } }: Props) => {
  const members = useAllMembers();
  const memberDri = useMemo(() => {
    return members.find(member => member.id === dri).name.ko;
  }, [dri, members]);
  const memberAdmin = useMemo(() => {
    return admin === 'Lubycon' ? '' : members.find(member => member.id === admin).name.ko;
  }, [admin, members]);

  const style = useStyles();

  return (
    <AnimatedListItem>
      <a href={url} target="_blank" rel="noopener noreferrer" className={style.root}>
        <Card>
          <CardActionArea>
            <Box display="flex" className={style.chips}>
              <Chip size="small" label={`DRI: ${memberDri}`} />
              <Chip color="primary" size="small" label={`: ${memberAdmin}`} />
            </Box>
            <CardContent>
              <Grid container direction="column" alignItems="center">
                <Grid item component="img" src={icon.childImageSharp.fixed.src} className={style.image} />
                <Grid item component={Typography} gutterBottom={true} className={style.name}>
                  {name}
                </Grid>
                <Grid item component={Typography} variant="caption">
                  {description}
                </Grid>
              </Grid>
            </CardContent>
          </CardActionArea>
        </Card>
      </a>
    </AnimatedListItem>
  );
};

export default ToolCard;
