import { Octokit } from '@octokit/rest';
import http from 'utils/http';
import { GetResponseDataTypeFromEndpointMethod } from '@octokit/types';

const Github = new Octokit();

type Runs = GetResponseDataTypeFromEndpointMethod<typeof Github.actions.listWorkflowRunsForRepo>;
type Commits = GetResponseDataTypeFromEndpointMethod<typeof Github.repos.listCommits>;
type BranchRef = GetResponseDataTypeFromEndpointMethod<typeof Github.git.getRef>;

export async function fetchDeployRuns() {
  return http.get<{ runs: Runs['workflow_runs']; manualDeployWorkflowId: number; cicdWorkflowId: number }>(
    '/prod/github/deploys'
  );
}

export async function fetchCommits() {
  return http.get<Commits>('/prod/github/commits');
}

export async function getBranchRef(branchName: string) {
  return http.get<BranchRef>(`/prod/github/branch-ref/${encodeURIComponent(branchName)}`);
}

export async function isExistBranch(branchName: string) {
  try {
    await getBranchRef(branchName);
    return true;
  } catch (e) {
    if (e.response.status === 404) {
      return false;
    } else {
      throw e;
    }
  }
}

export async function createBranch(branchName: string, commitSha: string) {
  return http.post('/prod/github/branch', {
    branchName,
    commitSha,
  });
}

export async function deploy(sha: string, workflowId: number) {
  const branchName = `deployed/${sha}`;
  const isExist = await isExistBranch(branchName);

  if (!isExist) {
    await createBranch(branchName, sha);
  }

  return http.post('/prod/github/deploy', {
    workflowId,
    branchName,
  });
}
