import React, { useCallback, useEffect, useState } from 'react';
import { LoginScheme } from 'models/auth';
import LubyconMemberForm from 'components/Forms/Login/LubyconMemberForm';
import { Alert } from '@material-ui/lab';
import { Collapse, makeStyles } from '@material-ui/core';
import { useAuth } from 'hooks/auth/useAuth';

const useStyles = makeStyles({
  alert: {
    margin: '2rem 0 0',
  },
});

const LoginForm = () => {
  const styles = useStyles();
  const { loginByEmail } = useAuth();
  const [errorMessage, setErrorMessage] = useState('');
  const [loginLoading, setLoginLoading] = useState(false);

  const loginAsLubyconMember = useCallback(
    async ({ email, password }: LoginScheme) => {
      setLoginLoading(true);
      try {
        await loginByEmail(email, password);
      } catch (e) {
        setErrorMessage(e.message);
      } finally {
        setLoginLoading(false);
      }
    },
    [loginByEmail]
  );

  useEffect(() => {
    if (errorMessage.length > 0) {
      setTimeout(() => {
        setErrorMessage('');
      }, 3000);
    }
  }, [errorMessage.length]);

  return (
    <>
      <LubyconMemberForm onSubmit={loginAsLubyconMember} loading={loginLoading} />
      <Collapse in={errorMessage.length > 0}>
        <Alert className={styles.alert} severity="error">
          {errorMessage}
        </Alert>
      </Collapse>
    </>
  );
};

export default LoginForm;
