import React from 'react';
import { Container, Grid, makeStyles, Theme, createStyles, useTheme, useMediaQuery } from '@material-ui/core';
import Page from 'components/Layout/Page';
import LoginForm from 'components/Forms/Login';
import { getLogoSrc } from 'src/utils/logo';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      paddingTop: theme.spacing(20),
      paddingBottom: theme.spacing(10),
      [theme.breakpoints.down('md')]: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(10),
      },
    },
    logo: {
      textAlign: 'center',
      marginBottom: '2.5rem',
      [theme.breakpoints.down('md')]: {
        marginBottom: '1rem',
      },
    },
    logoImage: {
      width: '80%',
      [theme.breakpoints.down('md')]: {
        width: '60%',
      },
    },
  })
);

export const Login = () => {
  const styles = useStyles();
  const theme = useTheme();
  const logoSrc = getLogoSrc(theme.palette.type, false);

  return (
    <Page pageName="">
      <Container className={styles.root}>
        <Grid justify="center" container>
          <Grid xs={12} md={6} lg={4} item>
            <div className={styles.logo}>
              <img src={logoSrc} alt="" className={styles.logoImage} />
            </div>
            <LoginForm />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default Login;
