import React, { useCallback } from 'react';
import Page from 'components/Layout/Page';
import { Container, Grid } from '@material-ui/core';
import { useDeployments } from 'src/hooks/useDeployments';
import DeploymentList from 'components/List/DeploymentList';

const DeploymentPage = () => {
  const { commits, lastRunCommitSha, workflowId, fetchData, setLastRunCommitSha } = useDeployments();

  const handleDeploy = useCallback(
    async (sha: string) => {
      await fetchData();
      setLastRunCommitSha(sha);
    },
    [fetchData, setLastRunCommitSha]
  );

  return (
    <Page pageName="Deployments" showTitle={true} description="    ">
      <Container>
        <Grid container justify="center">
          <Grid item xs={12} lg={8}>
            <DeploymentList>
              {commits.map((rawCommit, index) => (
                <DeploymentList.Item
                  key={index}
                  message={rawCommit.commit.message}
                  date={rawCommit.commit.committer.date}
                  sha={rawCommit.sha}
                  url={rawCommit.html_url}
                  isDeployed={lastRunCommitSha === rawCommit.sha}
                  workflowId={workflowId}
                  underline={index < commits.length - 1}
                  onDeployed={handleDeploy}
                />
              ))}
            </DeploymentList>
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default DeploymentPage;
